import React, { useState } from 'react'
import './Login.css'
import { Form, Button } from 'react-bootstrap'
import { Link } from "react-router-dom";
import { useForm } from 'react-hook-form'

function Login() {
    const { register, handleSubmit, formState: { errors } } = useForm();
    const onSubmit = (data) => console.log(data);

    return (
        <div className='login_page'>
            <div className='login_details'>
                <h5 className='text-center mb-4'>Welcome to Motor progear</h5>
                <Form onSubmit={handleSubmit(onSubmit)}>
                    <Form.Group>
                        <Form.Label>Email</Form.Label>
                        <Form.Control type='email' name='email' id='mail' placeholder='Email' {...register('mail', { required: true })}></Form.Control>
                        {errors.mail && <p className='text-danger'>Email is required.</p>}
                    </Form.Group>
                    <Form.Group className='mt-1'>
                        <Form.Label>Password</Form.Label>
                        <Form.Control type='password' name='password' id='pass' placeholder='Enter Password' {...register('password', { required: true })}></Form.Control>
                        {errors.password && <p className='text-danger'>Password is required.</p>}
                    </Form.Group>
                    <Form.Group className='mt-3'>
                        <Button variant='dark' size='sm' type='submit'>Login</Button>
                        <Link to='/forgotPassword' className='forgot'>Forgot Password?</Link>
                    </Form.Group>
                    <p className='text-muted text-center mt-3'>New to Motor Progear? <Link to='/signuphere'>Create an account</Link></p>
                </Form>
            </div>
        </div >
    )
}

export default Login;