import React from 'react'
import './ForgotPassword.css'
import { Form, Button } from 'react-bootstrap'

function ForgotPassword() {
    return (
        <div className='forgotPassword_page'>
            <div className='forgotPassword_details'>
                <h5 className='text-center mb-4'>change the password here</h5>
                <Form>
                    <Form.Group>
                        <Form.Label>Email</Form.Label>
                        <Form.Control type='email' name='email' id='mail' placeholder='Email'></Form.Control>
                    </Form.Group>
                    <Form.Group className='mt-3 otp_btn'>
                        <Button variant='dark' size='sm' type='submit'>Send OTP</Button>
                    </Form.Group>
                    <p className='text-muted text-center mt-3'>Verfication code will be sent to your mail</p>
                </Form>
            </div>
        </div >
    )
}

export default ForgotPassword;