import React from 'react'
import { Container, Form, Button, Image } from 'react-bootstrap';
import { Link } from "react-router-dom";
import './Header.css'

function Header() {
    return (
        <div className='top'>
            <Container className='logo'>
                <h5>Motor Progear</h5>
                <Form.Control type='text' size='sm'></Form.Control>
                <i class="fas fa-search" id='search_btn'></i>
                <div className='add_user ms-auto'>
                    <Link to='/login'>
                        <Image src='http://cdn.onlinewebfonts.com/svg/img_509043.png' className='me-2' width='21px' title="Log in"></Image>
                    </Link>
                    <Link to='/signup'>
                        <Image src='https://i7.uihere.com/icons/583/29/127/sign-up-and-add-friends-82adf982a734c971a8834e1fee79685c.png' width='26px' title='Sign up'></Image>
                    </Link>
                    <Link to=''>
                        <Image src='https://cdn.icon-icons.com/icons2/1369/PNG/512/-shopping-cart_90604.png' width='22px' className='ms-2' title='Cart'></Image>
                    </Link>
                    <Link to='/about'>
                        <Image src='https://www.freeiconspng.com/thumbs/about-us-icon/information-about-us-icon-16.png' width='22px' title='Click here to know about Motor Progear' className='ms-5'></Image>
                    </Link>
                </div>
            </Container>
        </div>
    )
}

export default Header;