import React from 'react'
import './Signup.css'
import { Form, Button } from 'react-bootstrap'
import { Link } from "react-router-dom";
import { useForm } from 'react-hook-form'

export default function Signup() {
    const { register, handleSubmit, formState: { errors } } = useForm();
    const onSubmit = (data) => console.log(data);
    return (
        <div className='signup_page'>
            <div className='signup_details'>
                <h5 className='text-center mb-4'>Register here</h5>
                <Form onSubmit={handleSubmit(onSubmit)}>
                    <Form.Group>
                        <Form.Label>First Name</Form.Label>
                        <Form.Control type='text' name='fname' placeholder='First Name' {...register('fname', { required: true })}></Form.Control>
                        {errors.fname && <p className='text-danger'>First Name is required.</p>}
                    </Form.Group>
                    <Form.Group className='mt-1'>
                        <Form.Label>Last Name</Form.Label>
                        <Form.Control type='text' name='lname' placeholder='Last Name' {...register('lname', { required: true })}></Form.Control>
                        {errors.lname && <p className='text-danger'>Last Name is required.</p>}
                    </Form.Group>
                    <Form.Group className='mt-1'>
                        <Form.Label>Email</Form.Label>
                        <Form.Control type='email' name='mail' placeholder='Email'{...register('mail', { required: true })}></Form.Control>
                        {errors.mail && <p className='text-danger'>Email is required.</p>}
                    </Form.Group>
                    <Form.Group className='mt-1'>
                        <Form.Label>Password</Form.Label>
                        <Form.Control type='password' name='password' placeholder='Enter Password' {...register('password', { required: true })}></Form.Control>
                        {errors.password && <p className='text-danger'>Password is required.</p>}
                    </Form.Group>
                    <Form.Group className='mt-1'>
                        <Form.Label>Confirm Password</Form.Label>
                        <Form.Control type='password' name='confirm_password' placeholder='Re-Enter Password' {...register('confirm_password', { required: true })}></Form.Control>
                        {errors.confirm_password && <p className='text-danger'>Confirm Password is required.</p>}
                    </Form.Group>
                    <Form.Group className='mt-3'>
                        <Button variant='dark' size='sm' type='submit'>Sign up</Button>
                    </Form.Group>
                    <p className='text-muted text-center mt-3'>Existing user? <Link to='/loginhere'>Log In</Link></p>
                </Form>
            </div>
        </div>
    )
}
