import React from 'react'
import './HelmetFilter.css'
import { Form, Button } from 'react-bootstrap'

export default function HelmetFilter() {
    return (
        <div className='filters p-3 mt-5'>
            <div className='department_brand mb-3'>
                <h6>Brands</h6>
                <ul>
                    <Form.Check type='checkbox' label='AGV'></Form.Check>
                    <Form.Check type='checkbox' label='AXOR'></Form.Check>
                    <Form.Check type='checkbox' label='AXXIS'></Form.Check>
                    <Form.Check type='checkbox' label='CABERG'></Form.Check>
                    <Form.Check type='checkbox' label='HJC'></Form.Check>
                    <Form.Check type='checkbox' label='LS2'></Form.Check>
                    <Form.Check type='checkbox' label='MT'></Form.Check>
                    <Form.Check type='checkbox' label='SHOEI'></Form.Check>
                </ul>
            </div>
            <div className='price_list mb-3'>
                <h6>Price</h6>
                <ul>
                    <Form.Check type='checkbox' label='Below Rs.1000'></Form.Check>
                    <Form.Check type='checkbox' label='Rs.1001 - Rs.2000'></Form.Check>
                    <Form.Check type='checkbox' label='Rs.2001 - Rs.5000'></Form.Check>
                    <Form.Check type='checkbox' label='Rs.5001 - Rs.8000'></Form.Check>
                    <Form.Check type='checkbox' label='Rs.8001 - Rs.10000'></Form.Check>
                    <Form.Check type='checkbox' label='Above Rs.10000'></Form.Check>
                    <Form.Group className='mt-2'>
                        <tr>
                            <td className='me-1'><Form.Control type='number' size='sm' placeholder='Min'></Form.Control></td>
                            <td>&nbsp;</td>
                            <td className='me-1'><Form.Control type='number' size='sm' placeholder='Max'></Form.Control></td>
                            <td>&nbsp;</td>
                            <td><Button variant='secondary' size='sm'>Go</Button></td>
                        </tr>
                    </Form.Group>
                </ul>
            </div>
            <div className='size'>
                <h6>Size</h6>
                <ul>
                    <Form.Check type='checkbox' label='XS'></Form.Check>
                    <Form.Check type='checkbox' label='S'></Form.Check>
                    <Form.Check type='checkbox' label='M'></Form.Check>
                    <Form.Check type='checkbox' label='L'></Form.Check>
                    <Form.Check type='checkbox' label='XL'></Form.Check>
                </ul>
            </div>
            <div className='discount'>
                <h6>Discount</h6>
                <ul>
                    <Form.Check type='checkbox' label='10% Off or more'></Form.Check>
                    <Form.Check type='checkbox' label='20% Off or more'></Form.Check>
                    <Form.Check type='checkbox' label='30% Off or more'></Form.Check>
                    <Form.Check type='checkbox' label='40% Off or more'></Form.Check>
                    <Form.Check type='checkbox' label='50% Off or more'></Form.Check>
                    <Form.Check type='checkbox' label='60% Off or more'></Form.Check>
                </ul>
            </div>
            <div className='color_picker'>
                <h6>Color Picker</h6>
                <ul>
                    <Form.Control type='color'></Form.Control>
                </ul>
            </div>
            <div className='availability'>
                <h6>Availability</h6>
                <ul>
                    <Form.Check type='checkbox' label='Inc. Out of stock'></Form.Check>
                </ul>
            </div>
        </div>
    )
}
