import React from 'react'
import { Navbar, Nav } from 'react-bootstrap';
import { Link } from "react-router-dom";
import './Navbars.css'

function Navbars() {

    return (
        <div>
            <Navbar className='navbar_container'>
                <Nav>
                    <Nav.Link className='product_list' as={Link} to='/'>Home</Nav.Link>
                    <Nav.Link className='product_list' as={Link} to='/helmet'>Helmet</Nav.Link>
                    <Nav.Link className='product_list' as={Link} to='/jacket'>Jackets</Nav.Link>
                    <Nav.Link className='product_list' as={Link} to='/pant'>Pants</Nav.Link>
                    <Nav.Link className='product_list' as={Link} to=''>Gloves</Nav.Link>
                    <Nav.Link className='product_list' as={Link} to=''>Boots</Nav.Link>
                    <Nav.Link className='product_list' as={Link} to=''>Backbags</Nav.Link>
                    <Nav.Link className='product_list' as={Link} to=''>Accessories</Nav.Link>
                </Nav>
            </Navbar>
        </div>
    )
}

export default Navbars;